#app2 {
}
#app2 .tab-bar {
  display: flex;
}
#app2 .tab-bar > li {
  border: 1px solid grey;
  width: 50%;
  text-align: center;
  line-height: 50px;
}
#app2 .tab-bar > li.selected {
  background:grey;
  color: white;
}
#app2 .tab-content {
    text-align: center;
    font-size: 100px;
}
#app2 .tab-content > li {
  display: none;
}
#app2 .tab-content > li.active {
  display: block;
}